@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  display: block;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  color: #e5e5e5;
  border: 3px solid #ffffff;
  border-radius: 30px;
  box-sizing: border-box;
}
.button--pink {
  color: #D22856;
  border-color: #D22856;
}
.button--pink:hover {
  color: #ffffff;
  background-color: #D22856;
}
.button--pink:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #D22856;
}
.button--silver {
  color: rgba(40, 54, 69, 0.1);
  border-color: rgba(40, 54, 69, 0.1);
}
.button--silver:hover {
  border-color: rgba(40, 54, 69, 0.3);
}
.button--silver:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: rgba(40, 54, 69, 0.3);
  border-color: rgba(40, 54, 69, 0.3);
}
.button-green {
  display: block;
  padding-top: 16px;
  padding-bottom: 14px;
  font-family: "Open Sans", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #2AAB6D;
  border: 2px solid #2AAB6D;
  border-radius: 1px;
}
.button-green:hover {
  background-color: #1C9B5E;
  border: 2px solid #1C9B5E;
}
.button-green:active {
  background-color: #1C9B5E;
  color: rgba(255, 255, 255, 0.3);
}
.button-green:disabled {
  background-color: #e5e5e5;
  border: 2px solid #e5e5e5;
}
.body {
  margin: 0 auto;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #283645;
  background-color: #ffffff;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.centered-wrapper {
  padding: 0 20px;
  box-sizing: border-box;
}
@media (min-width: 660px) {
  .centered-wrapper {
    width: 660px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
