  z-index: 1;
}
@media (min-width: 660px) {
  .main-nav__list {
    top: 109px;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    top: 0;
    left: auto;
    right: 90px;
    display: flex;
  }
}
.main-nav__wrapper {
  position: relative;
}
@media (min-width: 1200px) {
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 5px;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 29px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-nav--closed .main-nav__toggle:hover::before {
  background-color: #D22856;
  box-shadow: 0 9px 0 0 #D22856, 0 16px 0 0 #D22856;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(210, 40, 86, 0.3);
  box-shadow: 0 8px 0 0 rgba(210, 40, 86, 0.3), 0 16px 0 0 rgba(210, 40, 86, 0.3);
}
.main-nav--opened .main-nav__toggle {
  top: 22px;
  right: 0;
  left: auto;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 12px;
  left: 23px;
  width: 30px;
  height: 3px;
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .main-nav--opened .main-nav__toggle {
    top: 40px;
  }
}
.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after {
  background-color: #D22856;
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.main-nav--closed .site-list__item {
