.promo {
  position: relative;
  overflow: hidden;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  &--page-index {
    @media (min-width: @tablet-width) {
      padding-bottom: 81px;
    }
  }

  &--page-catalog {
    display: block;
    padding-top: 28px;
    padding-bottom: 76px;

    @media (min-width: @tablet-width) {
      padding-top: 64px;
      padding-bottom: 128px;
    }

    @media (min-width: @desktop-width) {
      padding-top: 58px;
      padding-bottom: 88px;
    }
  }

  &--page-form {
    padding-bottom: 43px;

    @media (min-width: @tablet-width) {
      padding-bottom: 61px;
    }

    @media (min-width: @desktop-width) {
      padding-bottom: 88px;
    }
  }
}

.promo__wrapper {
  @media (min-width: @tablet-width) {
    grid-column: 1 /-1;
    grid-row: 1 /-1;
  }
}

.promo__title {
  margin: 0 auto;
  margin-top: 23px;
  margin-bottom: 29px;
  font-size: 24px;
  line-height: 33px;
  font-weight: 300;
  text-align: center;
  color: @white;

  &--index {
    width: 280px;
    line-height: 29px;

    @media (min-width: @tablet-width) {
      width: 240px;
      grid-column-start: 1;
      grid-column-end: 1;
      grid-row-start: 1;
      grid-row-end: 1;
    }
  }

  @media (min-width: @tablet-width) {
    margin: 0;
    margin-top: 95px;
    margin-left: 30px;
    font-size: 40px;
    line-height: 48px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 97px;
    margin-left: 40px;
    font-size: 50px;
    line-height: 68px;
  }

  &--catalog {
    width: 178px;
    margin: 0 auto;

    @media (min-width: @tablet-width) {
      width: 358px;
      font-size: 50px;
      line-height: 68px;
    }
  }

  &--form {
    width: 228px;
    margin: 0 auto;

    @media (min-width: @tablet-width) {
      width: 500px;
    }

    @media (min-width: @desktop-width) {
      width: 747px;
    }
  }
}

.promo__background {
  display: block;
  margin: 0 auto;
  margin-left: -20px;

  @media (min-width: @tablet-width) {
    position: absolute;
    top: 73px;
    right: 0;
    grid-column-start: 2;
    grid-row-start: 1;
    grid-row-end: 3;
    width: 660px;
    height: 545px;
    margin-left: 0;
  }

  @media (min-width: @desktop-width) {
    top: 95px;
    right: 5px;
    width: 1050px;
    height: 645px;
  }
}
